@mixin pad {
  @media (max-width: 992px) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: 767px) {
    @content;
  }
}

@function getColor($colorName) {
  $color: map-get($colors, $colorName);
  @return $color;
}

@mixin animationBase($name, $duration) {
  animation-name: $name;
  animation-duration: $duration;
}